.header{
    padding: 35px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.187);
}

.nav{
    width: 60%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.nav a{
    text-decoration: none;
    font-size: 20px;
    font-weight: bold;
    color: rgb(54, 203, 203);
}

.nav a.active{
    color: red;
}
