.footer{
    padding: 35px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.buttoncontainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 50%;
}

button {
    border: 3px solid rgb(60, 222, 222);
    border-radius: 10px;
    width: 90px;
    padding: 10px;
    background-color: transparent;
    color: rgb(54, 203, 203);
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
}